import React, {useState, useEffect, useContext} from "react"
import PropTypes from "prop-types"
import {Link} from "react-router-dom"
import {CSSTransition} from "react-transition-group"
import {withRouter} from "react-router"

import {Button} from "semantic-ui-react"
import {Container} from "semantic-ui-react"
import {Header} from "semantic-ui-react"
import {Divider} from "semantic-ui-react"
import {clientID, redirectUri} from "./settings"
import AuthContext from "../AuthContext"
import { SpotifyAuth } from "react-spotify-auth"
import Cookies from 'js-cookie'

import "../GlobalStyle.css"
import "./styles.css"

const LandingPage = (props) => {
    const {loggedIn, accessToken, setLoggedIn, setToken} = useContext(AuthContext)
    const [isLoaded, setLoaded] = useState(false)

    useEffect(() => {
      const token = Cookies.get('spotifyAuthToken')
      if (token) {
        setLoggedIn(true)
        setToken(token)
      }
  }, [])

    useEffect(() => {
        const token = Cookies.get('spotifyAuthToken')
        if (token) {
          setLoggedIn(true)
          setToken(token)
        }
    }, [Cookies.get('spotifyAuthToken')])
    

    return (
        <CSSTransition classNames='fade' timeout={300} in={isLoaded}> 
          <Container 
            style={{
              display: "flex", 
              background: "rgb(127,24,161)",
              flexDirection: "column", 
              alignItems: "center", 
              justifyContent: "center", 
              height: "100vh",
            }} 
          >
            <Header as='h1' id="title">
              
            </Header>
            <p id="subtitle" > 
              Horoscoped Playlists 
            </p>
            {!loggedIn ? (
              <Button 
                id="logIn"
                as={SpotifyAuth} 
                clientID={clientID}
                redirectUri={redirectUri}
                scopes={["playlist-modify-public,playlist-modify-private,user-top-read"]}
                showDialog={true}
                noLogo={true}
                size='massive'
                content='Log In to Spotify'/>
  
            ) : (
              <Button 
                as={ Link }
                onClick={() => setLoaded(false)}
                to='/scopify/step1' 
                size='massive'
                style={{margin: "1rem"}} 
                content='Get Started' />
            )}
            <Divider />
        </Container>
      </CSSTransition>
    )
}

LandingPage.propTypes = {
}

export default withRouter(LandingPage)



